import uniIcon from "@/components/uni/uni-icon/uni-icon.vue"
import {  
    mapMutations  
} from 'vuex';
export default {
	components: {uniIcon},
	data(){
		return {
			xgnc:false,
			from:{
				headpic:0,
				nickname:'',
				sex:'',
				birthday:'',
			},
			current:1
		}
	},
	onLoad(options) {
		this._load()
	},
	methods:{
		set(){
			this.$api.http.post('user/edit_info',this.from).then(res=>{
				console.log(res)
				this.$api.msg('操作成功')
				setTimeout(()=>{
					uni.switchTab({
						url: '/pages/user/user',
					});
				},1500)
				
			})
			
		},
		_load() {
			// this.$api.loading()
			this.$api.http.get('user/info').then((res)=>{
				this.from = res	 
				uni.hideLoading()
			});
		},
		
		radioChange(e){
			this.from.sex=e.detail.value
		},
		change_nc(){
			this.xgnc=!this.xgnc
		},
		...mapMutations(['logout']),

		navTo(url){
			this.$api.msg(`跳转到${url}`);
		},
		//退出登录
		toLogout(){
			uni.showModal({
			    content: '确定要退出登录么',
			    success: (e)=>{
			    	if(e.confirm){
			    		this.logout();
			    		uni.clearStorageSync();
			    		uni.reLaunch({
			    			url:'/pages/public/login'
			    		})
			    	}
			    }
			});
		},
		//switch
		switchChange(e){
			let statusTip = e.detail.value ? '打开': '关闭';
			this.$api.msg(`${statusTip}消息推送`);
		},

	},
	onPullDownRefresh() {
		console.log('refresh');
		this._load();
		setTimeout(function () {
			uni.stopPullDownRefresh();
		}, 2000);
	}
}
